
import axios from 'axios'

export default{
  
 
    //actions
    addItem({commit},item){
        
        axios.post("http://localhost:8082/tolistentitys",item).then(
            res=>{
              
            }
          ).catch(reason => {
                alert(reason)
              });
    },

    getAllItem({commit}){

    axios.get("http://localhost:8082/tolistentitys").then(
     res=>{
         if(res.data.length>0){
            res.data.map(e=>{
                console.log(e);
                let item={};
                item.id=e.id;
                item.title=e.title;
    
                if(e.isChecked===0){
                    item.state=false;
                }else{
                    item.state=true;
                }
                if(e.isEven===0){
                    item.isEven=false;
                }else{
                    item.isEven=true;
    
                }
    
                commit("addTodos1",item);
    
    
            }) 
         }
        
     }
   ).catch(reason => {
                alert(reason)
    });
    }
     
}
